ul,li{list-style: none;margin: 0;padding: 0;
}
.home{
		width: 800px;
		height: auto;
		margin: 20px auto;
		/*border: 1px solid #928E8E;*/
		padding: 20px;
}
.home input[type="text"]{
		width: 400px;
		height: 40px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		padding: 7px 10px;
	    line-height: 26px;
	    border: 1px solid #c0ccda;
		outline: none;
		border-radius: 4px;
}
.add-btn{
		width: 80px;
	    height: 40px;
	    line-height: 26px;
	    color: #fff;
	    border: none;
    	background: #00b0f0;
    	border-radius: 4px;
    	outline: none;
}
.theme-bg .theme-list{
		position: relative;
		width: 70px;
	    height: 36px;
	    line-height: 36px;
	    padding-left: 36px;
	    border: 1px solid #aaa;
	    border-radius: 3px;
	    cursor: pointer;
        background: #fff;
        float: left;
        margin-right: 10px;
}
.theme-bg .theme-list span{
		position: absolute;
	    left: 8px;
	    top: 8px;
	    width: 20px;
	    height: 20px;
}

input[type="checkbox"]{
    width: 20px;
	height: 20px;
	vertical-align: middle;
}
.title{
	margin: 10px 0px;
	font-size: 16px;
	font-weight: 300;
	background: #00b0f0;
	color: #FFFFFF;
	height: 40px;
	line-height: 40px;
	padding-left: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.cancel{
	display: inline-block;
	border: 1px solid #928E8E;
	padding: 4px 6px;
}
ul li{
	margin-bottom: 5px;
}

.hide-search{
		width: 100%;
		height: 35px;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 221;
		background: #fff;
		-webkit-transform: translateY(-600px);
		        transform: translateY(-600px);
		-webkit-transition: -webkit-transform .4s;
		transition: -webkit-transform .4s;
		transition: transform .4s;
		transition: transform .4s, -webkit-transform .4s;
}
.edit-input-show{
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
.hide-search input{
		width: calc(100% - 85px) !important;
		height: 35px !important;
}
.hide-search button{
		width: 80px;
		height: 35px;
		border: none;
		/*background: #00b0f0;*/
		color: #fff;
		border-radius: 3px;
}
.search{
		margin-top: 30px;
		width: 100%;
		height: auto;
		/*overflow: hidden;*/
}
.searth-top{
		width: 100%;
		height: 35px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		margin-bottom: 10px;
		position: relative;
}
.div-select{
		position: absolute;
	    left: 0;
	    top: 0;
	    width: 85px;
	    height: 35px;
	    padding: 0 10px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    border: 1px solid #eee;
	    font-size: 12px;
	    color: #999;
	    cursor: pointer;
	    z-index: 22;
}
.div-select:after{
		position: absolute;
	    right: 6px;
	    top: 12px;
	    width: 6px;
	    height: 6px;
	    content: '';
	    border-right: 1px solid #999;
	    border-bottom: 1px solid #999;
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
}
.div-value{
		line-height: 35px;
}
.div-select-body{
		position: absolute;
	    left: 0;
	    top: 38px;
	    width: 85px;
	    height: 0;
	    padding-left: 10px;
	    line-height: 30px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    overflow: hidden;
	    z-index: 10;
	    -webkit-box-shadow: none;
	    box-shadow: none;
	    border: none;
	    border-radius: 3px;
	    background: #fff;
	    -webkit-transition: all .3s;
	    transition: all .3s;
}
.active .div-select-body{
		height: 122px;
		border: 1px solid #eee;
	    -webkit-box-shadow: 0 0 1px #ddd;
	    box-shadow: 0 0 1px #ddd;
}
.search-search{
		width: calc(100% - 95px) !important;
		position: absolute;
		top: 0;
		right: 0px;
		height: 35px !important;
		padding: 5px 10px !important;
		border-radius: 0 !important;
		border: 1px solid #eee !important;
}
.search-search:focus{
		border: 1px solid green !important;
}
table{
		width: 100%;
		border-left: 1px solid #eee;
    	border-top: 1px solid #eee;
    	padding: 0;
    	border-spacing: 0px;
}
table th,table td{
		height: 40px;
		box-sizing: border-box;
		padding: 5px 10px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    text-overflow: ellipsis;
	    vertical-align: middle;
	    border-right: 1px solid #eee;
	    border-bottom: 1px solid #eee;
	    text-align: center;
}

























































































































